.viewer {
  height: 75%;
  position: relative;
  margin: auto;
  background-image: url('../images/retro_tv.png');
  background-repeat: no-repeat;
  background-size: contain;
  aspect-ratio: 1006 / 635;
}

.viewer > .inner-screen {
  height: 61%;
  width: 48.1%;
  position: relative;
  top: 22.5%;
  left: 10.2%;
}

.viewer > .inner-screen > .ytb-player {
  width: 100%;
  height: 100%;
}

.viewer > .inner-screen > .progress-bar {
  position: relative;
  top: 35%;
}

.viewer > .btn-panel {
  position: absolute;
  background-color: white;
  width: 12.4%;
  right: 2%;
  height: 15%;
  min-height: 7.7em;
  top: 11.8%;
  border-radius: 1em;
  overflow: hidden;
}

.viewer > .btn-panel > .grid {
  margin: 0;
  height: 100%;
}

.viewer > .btn-panel > .grid > .row {
  padding: 0;
  height: 33.333%;
}

.viewer > .btn-panel > .grid > .row > .column {
  padding: 0;
}

.viewer > .btn-panel > .grid > .row > .column > .button {
  width: 100%;
  height: 100%;
  border-radius: 0;
  padding: 0;
}

.viewer > .scores-panel {
  display: flex;
  flex-direction: column;
  gap: 1em;
  position: absolute;
  width: 12.4%;
  right: 2%;
  height: 55%;
  top: 31.8%;
  overflow: auto;
}

.viewer > .scores-panel > .label {
  border-radius: 1em;
}

.viewer > .scores-panel > .label > .detail {
  float: right;
}

.viewer > .reset-btn {
  position: absolute;
  width: 12.4%;
  right: 2%;
  top: 90%;
  overflow: auto;
}

.viewer > .teams-btn {
  position: absolute;
  width: 12.4%;
  right: 2%;
  top: 84%;
  overflow: auto;
}

.flex-lists {
  margin-top: 1em;
  display: flex;
  gap: 1em;
  justify-content: space-evenly;
}

.flex-lists > .ui.list {
  margin: 0;
}

.ui.label > .close.icon {
  margin-left: 0;
}